import React, {Component} from 'react';
import RaisedButton from 'material-ui/RaisedButton';
import Dialog from 'material-ui/Dialog';
import {deepOrange500} from 'material-ui/styles/colors';
import FlatButton from 'material-ui/FlatButton';
import getMuiTheme from 'material-ui/styles/getMuiTheme';
import MuiThemeProvider from 'material-ui/styles/MuiThemeProvider';
import AppBar from 'material-ui/AppBar';
import {Tabs, Tab} from 'material-ui/Tabs';
import Slider from 'material-ui/Slider';
import TimePicker from 'material-ui/TimePicker';
import Checkbox from 'material-ui/Checkbox';
import DropDownMenu from 'material-ui/DropDownMenu';
import MenuItem from 'material-ui/MenuItem';
import {GridList, GridTile} from 'material-ui/GridList';
import IconButton from 'material-ui/IconButton';
import Subheader from 'material-ui/Subheader';
import StarBorder from 'material-ui/svg-icons/toggle/star-border';
import Drawer from 'material-ui/Drawer';
import IconMenu from 'material-ui/IconMenu';
import MoreVertIcon from 'material-ui/svg-icons/navigation/expand-more';
import Menu from 'material-ui/Menu';
import Popover from 'material-ui/Popover';
import Divider from 'material-ui/Divider';
import FontIcon from 'material-ui/FontIcon';
import ContentCopy from 'material-ui/svg-icons/content/save';
import ContentUpload from 'material-ui/svg-icons/file/file-download';
import {List, ListItem} from 'material-ui/List';
import Toggle from 'material-ui/Toggle';
import 'whatwg-fetch';




const styles = {
  root: {
    display: 'flex',
    flexWrap: 'wrap',
    justifyContent: 'space-around'
  },
  gridList: {
    width: '90%',
    height: 220,

    marginBottom: 0,
    marginLeft: '3%'
  },
};


const muiTheme = getMuiTheme({
  palette: {
    accent1Color: deepOrange500,
  },
});

class Main extends Component {
  constructor(props, context) {
    super(props, context);

    // this.handleRequestClose = this.handleRequestClose.bind(this);
    // this.handleTouchTap = this.handleTouchTap.bind(this);

    this.handleToggle = () => this.setState({ open: !this.state.open });

    /*
     setInterval(function() 
        {
          this.setState({value: this.state.value+1})
      }.bind(this) , 10);
      */

    this.handleMenuItem0Close = () => { this.setState({ open: false, menuItemIndex: 0 }); }

    this.handleMenuItem1Close = () => this.setState({ open: false, menuItemIndex: 1 });

    //this.tiles = new Array(200)

    this.state =
      {
        menuItemIndex: 1,
        open: false,
        value: 1,
        tiles: [{}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}]
      };
    this.menuItemNames = ['Chauffage', 'Volets'];
    this.menuItemName = () => this.menuItemNames[this.state.menuItemIndex];

  }

  /*
    handleRequestClose() {
      this.setState({
        open: false,
      });
    }
  
    handleTouchTap() {
      this.setState({
        open: true,
      });
    }
    handleFirstSlider(event, value) {
      this.setState({ value: value });
    }
    */

  render() {

    //function menuItemChanged (event, value) {console.log(value)}
    return (
      <MuiThemeProvider muiTheme={muiTheme}>
        <div>
          <Drawer
            docked={false}
            width={200}
            open={this.state.open}
            onRequestChange={(open) => this.setState({ open }) }
            >
            <AppBar
              style={{ backgroundColor: '#028cb6' }}
              title=""
              onLeftIconButtonTouchTap={this.handleToggle}
              />

            <MenuItem onTouchTap={this.handleMenuItem0Close}>{this.menuItemNames[0]}</MenuItem>
            <MenuItem onTouchTap={this.handleMenuItem1Close}>{this.menuItemNames[1]}</MenuItem>

          </Drawer>

          <AppBar
            style={{ backgroundColor: '#028cb6' }}
            title={this.menuItemName() }
            onLeftIconButtonTouchTap={this.handleToggle}
            iconElementRight={
              <IconMenu
                iconButtonElement={<IconButton><MoreVertIcon /></IconButton>}
                targetOrigin={{ horizontal: 'right', vertical: 'top' }}
                anchorOrigin={{ horizontal: 'right', vertical: 'top' }}
                >
                <MenuItem color="red" primaryText="Enregistrer" rightIcon={<ContentCopy />}/>
                <Divider />
                <MenuItem primaryText="Vacances" rightIcon={<ContentUpload />}/>
                <MenuItem primaryText="Travail" rightIcon={<ContentUpload />}/>
                <MenuItem primaryText="Eau chaude" rightIcon={<ContentUpload />}/>
              </IconMenu>
            }
            />

          {(() => {
            if (this.state.menuItemIndex == 0) {
              return (
                <Tabs inkBarStyle={{ backgroundColor: '#028cb6' }}>
                  <Tab style={{ color: '#028cb6', backgroundColor: 'rgb(235,235,235)' }} label="Semaine" >
                    <br/>
                    <Grid />
                  </Tab>
                  <Tab style={{ backgroundColor: 'rgb(235,235,235)', color: '#028cb6' }} label="Temperatures" >
                  </Tab>
                </Tabs>)
            }
            else {
              return (
                <RollingShutter/>)
            }
          })() }
        </div>
      </MuiThemeProvider>
    );
  }
}
export default Main;

class RollingShutter extends GridList {
  constructor(props, context) 
  {
    super(props, context);
   // this.state = {ee:1}

    this.state =
    { status:
      [
        {
          "open": false,
          "order": 0
        },
        {
          "open": true,
          "order": 1
        },
        {
          "open": false,
          "order": 2
        },
        {
          "open": true,
          "order": 3
        }
      ]
    }
      
  }


componentDidMount() 
{
     fetch('http://domo.emillet.net/rollingShutter', {method: 'GET'})
.then(function(response) {return(response.json());}.bind(this))
.then( function(json) { var js = {status:json}; this.setState(js);}.bind(this))
}



  render() {
    console.log(this.state);
    return (
      <List style={{ width: '90%', marginLeft: '5%' }} >
        <br />
        {/*<Subheader>Priority Interruptions</Subheader>*/}
        <ListItem primaryText="Volet salon" secondaryText="Ouvert" rightToggle={<Toggle onToggle={function () {
          var localState = this.state.status[2].open == true ? false : true;
          var bodyJson = JSON.stringify({ open: localState, order: 2 });
          fetch('http://domo.emillet.net/rollingShutter', {method: 'POST', headers: {'Accept': 'application/json', 'Content-Type': 'application/json'}, body: bodyJson})
            .then(function (response) { return (response.json()); }.bind(this))
            .then(function (json) { var js = { status: json }; this.setState(js); }.bind(this))
        }.bind(this)
       
       } toggled={this.state.status[2].open}/>} />
        <Divider/>
        <ListItem primaryText="Volet salle  manger" secondaryText="Ferm" rightToggle={<Toggle />} />
        <Divider/>
        <ListItem primaryText="Volet bureau" secondaryText="Ferm" rightToggle={<Toggle />} />
        <Divider/>
        <ListItem primaryText="Volet cuisine" secondaryText="Ferm" rightToggle={<Toggle />} />
        <Divider/>
      </List>
    )
  }
}

class Grid extends GridList {
  constructor(props, context) {
    super(props, context);
    this.state =
      {
        tiles: [{}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}]
      };
  }
  render() {
    var handleTouchTap = (tileIndex) => { /*console.log(tileIndex)*/ }
    return (
      <GridList cellHeight={30} style={styles.gridList} cols = {8}>
        {this.state.tiles.map((tile, i) => {
          if (i < 8 || i == 192) return <DayTile key={i} index={i} />
          else if (i % 8 == 0) return <TimeTile key={i} index={i} />
          else return <MainTile callback={handleTouchTap} key={i} index={i} />
        })
        }
      </GridList>
    )
  }
}


class MainTile extends Component {
  constructor(props, context) {
    super(props, context);
    this.state =
      {
        index: props.index ? props.index : 0,
        open: false,
      }

    this.handleTouchTap = (event) => {
      // This prevents ghost click.
      event.preventDefault();

      this.setState({
        open: true,
        anchorEl: event.currentTarget,
      });
      this.props.callback(this.state.index)
    };
    this.handleRequestClose = () => {
      this.setState({
        open: false,
      });
    };
  }


  render() {

    return (
      <button type='button' onClick={this.handleTouchTap} style={
        this.state.open ? {marginTop:'3px',marginLeft: '8%', width: '84%', height: '24px', backgroundColor: 'Grey', borderRadius: '2px', border: 'none' }
          : { width: '100%', height: '100%', backgroundColor: 'Grey', borderRadius: '2px', border: 'none' }} >
        <h1 style={{ height: '68%', fontSize: '100%', textAlign: 'center', position: 'relative', top: '16%', color: 'white' }}>
          {(() => {
            var hoursInWeek = 24 * (this.state.index % 8) - 25 + Math.floor(this.state.index / 8);
            var refDate = new Date(2016, 8, 12)
            var cellDate = new Date(refDate.getTime() + hoursInWeek * 60 * 60 * 1000)
            //     return this.state.index
          })() }
        </h1>

        <Popover
          open={this.state.open}
          anchorEl={this.state.anchorEl}
          anchorOrigin={{ horizontal: 'left', vertical: 'bottom' }}
          targetOrigin={{ horizontal: 'left', vertical: 'top' }}
          onRequestClose={this.handleRequestClose}
          >
          <Menu>
            <MenuItem primaryText="Refresh" />
            <MenuItem primaryText="Help &amp; feedback" />
            <MenuItem primaryText="Settings" />
            <MenuItem primaryText="Sign out" />
          </Menu>
        </Popover>

      </button>

    )
  }
}


class TimeTile extends Component {
  constructor(props, context) {
    super(props, context);
    this.state =
      {
        index: props.index ? props.index : 0
      }
  }
  render() {
    var tileStyle = () => {
      var date = new Date();
      var todayHour = date.getHours();
      var currentRow = Math.floor(this.state.index / 8)
      if (currentRow == todayHour || currentRow == todayHour + 1) return { color: '#028cb6', height: '68%', fontSize: '120%', textAlign: 'right', position: 'relative', top: '50%', right: '10%' }
      else return { height: '68%', fontSize: '120%', textAlign: 'right', position: 'relative', top: '50%', right: '10%' }
    };
    return (
      <div style={{ width: '100%', height: '100%', backgroundColor: 'white', border: 'none' }} >
        <h1 style={tileStyle() } >
          {(() => {
            var time = Math.floor(this.state.index / 8);
            return time > 9 ? time.toString() + 'h' : '0' + time.toString() + 'h'
          })() }
        </h1>
      </div>
    )
  }
}


class DayTile extends Component {
  constructor(props, context) {
    super(props, context);
    this.state =
      {
        index: props.index ? props.index : 0
      }
  }
  render() {
    var tileStyle = () => {
      var date = new Date();
      var todayColumn = date.getDay();
      todayColumn = todayColumn == 0 ? 7 : todayColumn;
      if (this.state.index == todayColumn) return { color: '#028cb6', height: '68%', fontSize: '130%', textAlign: 'center', position: 'relative', bottom: '8%' }
      else return { color: 'black', height: '68%', fontSize: '130%', textAlign: 'center', position: 'relative', bottom: '8%' }
    };
    return (
      <div style={{ width: '100%', height: '100%', backgroundColor: 'white', border: 'none' }} >
        <h1 style={tileStyle() }>
          {(() => { return ['', 'L', 'M', 'M', 'J', 'V', 'S', 'D'][this.state.index] })() }
        </h1>
      </div>
    )
  }
}



